import path from 'path';
import merge from 'webpack-merge';
import webpack from 'webpack';
import common from './webpack.common.js';
import getConfigAsync from './utils/getConfigAsync';

import createDevServerConfigAsync from './createDevServerConfigAsync';
import { Environment, Arguments } from './types.js';

export default async function(env: Environment, argv: Arguments): Promise<webpack.Configuration> {
  if (!env.config) {
    // Fill all config values with PWA defaults
    env.config = await getConfigAsync(env);
  }

  const devServer = await createDevServerConfigAsync(env, argv, false);
  return merge(await common(env, argv), {
    output: {
      // Add comments that describe the file import/exports.
      // This will make it easier to debug.
      pathinfo: true,
      // Give the output bundle a constant name to prevent caching.
      // Also there are no actual files generated in dev.
      filename: 'static/js/bundle.js',
      // There are also additional JS chunk files if you use code splitting.
      chunkFilename: 'static/js/[name].chunk.js',
      // Point sourcemap entries to original disk location (format as URL on Windows)
      devtoolModuleFilenameTemplate: (info: webpack.DevtoolModuleFilenameTemplateInfo) =>
        path.resolve(info.absoluteResourcePath).replace(/\\/g, '/'),
    },
    devServer,
  });
}
